.game-result-container {
  min-height: calc(100vh - $footer-height);
  @include centerChilds;
  display: flex;
  flex-direction: column;
  color: $white-color;
  gap: 25px;
  $result-size: 3rem;
  transition: opacity 0.5s ease-in-out;
  h3 {
    font-size: $result-size;
  }
  h2 {
    font-size: calc($result-size + 1.5rem);
  }
  button {
    margin-top: 50px;
    @include btnStyle;
    font-size: $result-size;
    padding-inline: 50px;
  }
}
