.home-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row wrap;
  gap: 10px 10%;
  min-height: calc(100vh - $footer-height);
  max-width: 1000px;
  margin-inline: auto;
  > div {
    padding: 10px;
    flex-basis: 40%;
    flex-grow: 1;
    &.logo-container {
      max-width: 500px;
      min-width: 250px;
      @include centerChilds;
      img {
        width: clamp(50px, 100%, 400px);
        aspect-ratio: 2401 / 1857;
      }
    }
    &.form-container {
      max-width: 500px;
      min-width: 250px;
      padding-top: 50px;
      form {
        display: flex;
        flex-flow: column nowrap;
        gap: 20px;
        .form-control,
        button {
          display: block;
          flex-grow: 1;
        }
        input {
          width: 100%;
          padding: 15px;
          border: none;
          outline: none;
          border-radius: 15px;
          font-size: 1rem;
        }
        button {
          margin-top: 20px;
          @include btnStyle;
          font-size: 1.65rem;
          border-color: white;
          color: white;
          background: transparent;
          &:hover {
            background: white;
            color: $primary-color;
          }
        }
      }
    }
  }
}
