.game-container {
  display: flex;
  flex-flow: row nowrap;
  padding-inline: 50px;
  .droppable-areas-container {
    flex-basis: 55%;
    flex-grow: 1;
    display: flex;
    flex-flow: row wrap;
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    user-select: none;
    .drop-area {
      position: relative;
      opacity: 1;
      background: black;
      .droppable-layer {
        position: absolute;
        inset: 0;
        display: block;
        z-index: 15;
      }
      img {
        transition: all 0.5s ease-in-out;
        width: 100%;
        height: 100%;

        &.target {
          position: absolute;
          z-index: 5;
          inset: 0px;
        }
        &.initial {
          position: relative;
          z-index: 10;
        }
        &.hide {
          opacity: 0;
          visibility: hidden;
        }
      }

      &:is(:nth-child(1), :nth-child(2)) {
        width: 100%;
      }
      &:is(:nth-child(3), :nth-child(4)) {
        width: 50%;
      }
      &:nth-child(3) {
        img.target {
          left: 1px;
        }
      }
    }
    .hint-image {
      position: absolute;
      width: 100%;
      height: 100%;
      transition: all 0.4s ease-in-out;
      visibility: hidden;
      z-index: 20;
      opacity: 0;
      &.visible {
        visibility: visible;
        opacity: 1;
      }
    }
    .timer {
      position: absolute;
      left: 20px;
      top: 20px;
      color: white;
      z-index: 14;
      font-size: 1.5rem;
      font-weight: bold;
      animation: heart 0.5s both infinite alternate;
    }
  }
}

@keyframes heart {
  0% {
    scale: 1;
  }
  100% {
    scale: 1.05;
  }
}
