.drop-target {
  transition: opacity 0.5s ease-in-out, visibility 0.8s ease-in-out;
  z-index: 15;
  position: absolute;

  padding: 2.5px;

  opacity: 0.7;
  visibility: visible;
  &.hide {
    opacity: 0;
    visibility: hidden;
  }
  span {
    border-radius: 50%;
    display: block;
    padding: 30px;
  }
}
