.draggable-item {
  flex-basis: 30%;
  flex-grow: 1;
  color: white;
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  padding: 15px 10px;
  font-weight: bold;
  header {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    .actions {
      display: flex;
      flex-flow: row wrap;
      button {
        display: block;
        margin: 20px;
        margin-inline-start: auto;
        // background: rgb(0 0 0 / 10%);
        @include btnStyle;
      }
    }
    p {
      padding-inline-start: 10px;
      margin-inline-end: auto;
      font-size: 1.4rem;
      transition: all 0.5s ease-in-out;
      &.positive span {
        color: lightgreen;
      }
      &.negative span {
        color: rgba(252, 33, 33, 0.651);
      }
    }
  }
  article {
    flex-grow: 1;
    display: flex;
    flex-flow: row nowrap;
    .bottles-count {
      flex-basis: 70%;
      flex-grow: 1;
      @include centerChilds;
      div {
        position: relative;
        @include centerChilds;
        align-items: flex-end;
        $bg-color: #8c6db3;
        img {
          width: clamp(50px, 100%, 140px);
          z-index: 2;
        }
        p {
          background: $bg-color;
          $triangleWidth: 30px;
          $triangleHeight: 46.2px;

          padding: 20px 50px 20px 70px;
          font-size: 2.5rem;
          position: relative;
          bottom: 10px;
          right: 20px;
          min-width: 11rem;
          &::after {
            content: "";
            display: block;
            position: absolute;
            right: calc(-1 * $triangleWidth);
            top: 0;
            width: 0;
            height: 0;
            border-style: solid;
            border-top: $triangleHeight solid transparent;
            border-bottom: $triangleHeight solid transparent;
            border-left: $triangleWidth solid $bg-color;
            border-right: 0;
          }
        }
      }
    }
    .btns-container {
      flex-basis: 30%;
      @include centerChilds;
      .button-wrapper {
        border: 2.5px solid #984899c2;
        border-radius: 15px;
        padding: 10px;
        button {
          display: block;
          width: 100%;
          background: transparent;
          border: none;
          font-size: 2rem;
          color: inherit;
          padding: 10px;
        }
      }
    }
  }
}

button {
  cursor: pointer;
}
