footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  padding: 25px 35px;

  div {
    width: 150px;
    @include centerChilds;
    img {
      width: 100%;
      &.three {
        aspect-ratio: 2401 / 1857;
      }
      &.logo {
        aspect-ratio: 1041 / 415;
      }
    }
    &:has(.logo) {
      width: 175px;
      margin-inline-start: auto;
    }
  }
}
