.score-board-container {
  padding: 30px 20px;
  h3 {
    font-size: 3rem;
    color: white;
    text-align: center;
    padding-block: 25px;
  }

  table {
    margin-top: 30px;
    margin-inline: auto;
    width: min(100%, 1500px);
    text-align: center;

    th,
    td {
      padding: 15px 10px;
      font-size: 1.1rem;
    }
    th {
      color: $primary-color;
      background: white;
      font-size: 1.25rem;
    }
    td {
      color: white;
      border-bottom: 1px solid white;
    }
  }

  button {
    @include btnStyle;
    @include centerChilds;
    font-size: 1.25rem;
    margin-inline-start: auto;
    margin-inline-end: 15px;
  }
}
